interface IHelloDirectiveScope extends ng.IScope {
    name:string;
}

function HelloDirective($timeout:ng.ITimeoutService):ng.IDirective {
    return {
        template: `<div>{{ name }}</div>`,
        scope: {},
        link: (scope:IHelloDirectiveScope)=> {
            scope.name = 'Nick';

            $timeout(()=> {
                scope.name = "Changed to Nicholas!";
            }, 2000);
        }
    }
}

HelloDirective.$inject = ["$timeout"];

export default function register(module:ng.IModule):void {
    module.directive('hello', HelloDirective);
}




